@import url("https://fonts.googleapis.com/css?family=Ubuntu|Nova+Mono");
/* define the fonts and colors used in the project
- Nova Mono for the timer 
- Ubuntu for the input's text

- #ECECEC for the application background
- #ca4f4f for the application theme (color and body's background)
- #ed7575 and #aa4343 as variations of the theme color
*/
:root {
  --font-timer: 'Nova Mono', monospace;
  --font-text: 'Ubuntu', sans-serif;
  --color-theme-white: #fff;
  --color-theme-lighter: #dc8a8a;
  --color-theme-light: #d15c5c;
  --color-theme-dark: #c43434;
  /* define a variable which is updated in the script to visually relay the amount of time left (by changing the linear-gradient used as background) */
  --gradient-height: 100%;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  font-family: var(--font-text);
  color: var(--color-theme-light);
  /* include a gradient around the color chosen for the body */
  background: -webkit-gradient(linear, left top, right top, from(var(--color-theme-light)), color-stop(var(--color-theme-dark)), to(var(--color-theme-light)));
  background: -webkit-linear-gradient(left, var(--color-theme-light), var(--color-theme-dark), var(--color-theme-light));
  background: -o-linear-gradient(left, var(--color-theme-light), var(--color-theme-dark), var(--color-theme-light));
  background: linear-gradient(to right, var(--color-theme-light), var(--color-theme-dark), var(--color-theme-light));
  /* center the application horizontally and include some vertical padding */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}


